      activeTab: 'sports', // ÈªòË™çÂ±ïÁ§∫È´îËÇ≤È§®ÊúÉÂ†¥Âú∞Âúñ
      eventSchedule: {
        '10/19': [
          { start: '08:30', end: '09:00', event: 'ÂèÉË≥ΩËÄÖÂ†±Âà∞' },
          { start: '09:00', end: '10:30', event: 'ÈñãÂπïÂºè' },
          { start: '10:30', end: '12:00', event: 'Coding, ‰ºÅÊ•≠ÂçöË¶ΩÊúÉ, Â®õÊ®Ç‰∫§ÊµÅÊ¥ªÂãï' },
          { start: '12:00', end: '13:30', event: 'ÂçàÈ§ê' },
          { start: '13:30', end: '18:00', event: 'Coding, ‰ºÅÊ•≠ÂçöË¶ΩÊúÉ, Â®õÊ®Ç‰∫§ÊµÅÊ¥ªÂãï' },
          { start: '18:00', end: '19:30', event: 'ÊôöÈ§ê' },
          { start: '19:30', end: '22:00', event: 'Coding, Â®õÊ®Ç‰∫§ÊµÅÊ¥ªÂãï' },
          { start: '22:00', end: '22:30', event: 'ÂÆµÂ§ú' },
          { start: '22:30', end: '24:00', event: 'Coding, ÂèÉË≥ΩËÄÖ‰ºëÊÅØ' },
        ],
        '10/20': [
          { start: '00:00', end: '08:00', event: 'Coding, ÂèÉË≥ΩËÄÖ‰ºëÊÅØ' },
          { start: '08:00', end: '09:00', event: 'Êó©È§ê' },
          { start: '09:00', end: '11:00', event: 'Coding, ‰ºÅÊ•≠ÂçöË¶ΩÊúÉ, Â®õÊ®Ç‰∫§ÊµÅÊ¥ªÂãï' },
          { start: '11:00', end: '11:50', event: 'ÂçàÈ§ê, Ê¥ªÂãïÊî§‰Ωç' },
          { start: '11:50', end: '15:10', event: 'ÂâµÂÆ¢‰∫§ÊµÅÁµÑÊ±∫Ë≥Ω' },
          { start: '12:10', end: '14:00', event: 'ÈªëÂÆ¢ÁµÑÂàùË≥Ω' },
          { start: '15:20', end: '17:30', event: 'ÈªëÂÆ¢ÁµÑÊ±∫Ë≥Ω' },
          { start: '17:30', end: '18:30', event: 'È†íÁçé, ÊäΩÁçé' },
          { start: '18:30', end: '19:30', event: 'ÈñâÂπïÂºè' },
        ],
      },
    };
  },
  methods: {
    updateTime() {
      let now = new Date();
      // const hours = now.getHours();
      // const minutes = now.getMinutes().toString().padStart(2, '0');
      // const formattedTime = `${hours}:${minutes}`;
      const day = `${now.getMonth() + 1}/${now.getDate()}`;

      
      this.currentTime = `${this.formatAMPM(now)}`;
      console.log(this.currentTime);
      this.currentTimeFlat = now.getHours() * 60 + now.getMinutes();
      console.log(this.currentTimeFlat)

      this.updateCurrentEvent(day, this.currentTimeFlat);
    },
    formatAMPM(date) {
      let hours = date.getHours();
      const minutes = date.getMinutes().toString().padStart(2, '0');
      const ampm = hours >= 12 ? 'pm' : 'am';
      hours = hours % 12;
      hours = hours ? hours : 12;
      const strTime = hours + ':' + minutes + ' ' + ampm;
      return strTime;
    },
    updateCurrentEvent(day, currentTimeFlat) {
      const schedule = this.eventSchedule[day];

      if (!schedule || (schedule && currentTimeFlat < 510)) {
        this.currentEvent = 'Ê¥ªÂãïÂ∞öÊú™ÈñãÂßã';
        return;
      }

      let eventFound = false;
      for (const event of schedule) {
        const [startHours, startMinutes] = event.start.split(':').map(Number);
        const [endHours, endMinutes] = event.end.split(':').map(Number);
        const eventStartFlat = startHours * 60 + startMinutes;
        const eventEndFlat = endHours * 60 + endMinutes;
        if (currentTimeFlat >= eventStartFlat && currentTimeFlat < eventEndFlat) {
          this.currentEvent = event.event;
          eventFound = true;
          break;
        }
      }

      if (!eventFound) {
        this.currentEvent = 'Ê¥ªÂãïÂ∑≤ÁµêÊùü';
      }
    },
  },
  mounted() {
    // ÊØèÂàÜÈêòÊõ¥Êñ∞‰∏ÄÊ¨°ÊôÇÈñìÂíåÊ¥ªÂãï
    this.updateTime();
    setInterval(this.updateTime, 10000);
  },
};
